import React from 'react';
import {Link} from 'react-router-dom';

export default function Hero() {
    return (
        <div classname="min-h-screen w-screen flex justify-center items-center flex-col-reverse">
            <div className="text-green text-center md:text-left">
               <h1 className="font-bold text-2xl"> 
                   We make a living by what we get We make a living by what we give
                 </h1>
               <Link to="/signin">
                 <button className="animate-float bg-green-400 px-10 py-3 text-xl uppercase mt-10 rounded-lg">
                    Donate Now
                  </button>
                </Link>
         </div>
         <div className="w-10/12 mx-auto flex flex-col md:flex-row-reverse items-center justify-between">
           <div className="w-full md:w-2/5"> 
           <img alt="gift" src="https://pbs.twimg.com/media/EgUdkcZWkAA7UHk.jpg" /> 
           
         </div>
         </div>
       
        </div>
    )
}
